import React from 'react';
import KolesaXLSvg from './kolesa-xl.inline.svg';

interface SVGProps {
  width?: string | number;
  height?: string | number;
  fill?: string;
}

const KolesaXLComponent: React.FC<SVGProps> = ({
  width = "100%",
  height = "100%",
  fill = "currentColor"
}) => (
  <KolesaXLSvg 
    style={{
      width,
      height,
      fill
    }}
  />
);

export default KolesaXLComponent;